.App {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  overflow-x: scroll;
  overflow-y: scroll;
  background: #e9ecef;
  background: var(--app-bg-color);
  background: var(--app-webkit-linear-gradient-bg);
  background: var(--app-moz-linear-gradient-bg);
  background: linear-gradient(to bottom right, #e9ecef, #ced4da);
  background: var(--app-linear-gradient-bg);
  display: block;
}

.header-wrapper {
  top: 0px;
  left: 0px;
  z-index: 10;
  display: flex;
  height: 10rem;
  width: 100%;
  align-items: center;
  justify-content: flex-start;
  padding: 0px;
}

.header-wrapper h1 {
  color: #2e3151;
  color: var(--header-color);
  margin-left: 3rem;
  padding: 1rem;
  font-size: 2.25rem;
  line-height: 2.5rem;
  font-weight: 700;
  --tw-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.weather-app-visual {
  width: 100%;
  display: inline-grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  column-gap: 1rem;
}

.map-container,
.loader-container {
  height: max(40rem, 55vh);
  width: max(250px, 60vw);
  background-color: #dee2e6;
  background-color: var(--container-bg-color);
  aspect-ratio: 1.5;
  overflow: hidden;
}

.map-container, .loader-container {
  grid-column: span 3 / span 3;
  margin-left: 2.25rem;
  display: inline-grid;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  border-radius: 0.75rem;
  padding: 1.5rem;
  --tw-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.leaflet-container {
  width: 600%;
  border-radius: 0.5rem;
}

.loader-container {
  display: flex;
  flex-direction: column;
}

.loader-container img {
  aspect-ratio: 3;
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader-container h1 {
  color: #2e3151;
  color: var(--header-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  line-height: 2rem;
}

/* .google-map {
  height: 100%;
  width: 150%;
  @apply col-span-4;
} */

.button-control-wrapper {
  background-color: #dee2e6;
  background-color: var(--container-bg-color);
  height: calc(290px + 2vh);
  width: calc(160px + 5rem);
  overflow: auto;
  grid-column: span 1 / span 1;
  border-radius: 0.75rem;
  padding: 1.5rem;
  --tw-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.button-control-wrapper h2 {
  color: #2e3151;
  color: var(--header-color);
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 800;
}

.button-control-container div {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
}

.button-control-container button {
  aspect-ratio: 4;
  width: 12rem;
  min-width: 80px;
  max-width: 350px;
  color: #2e3151;
  color: var(--text-color);
  border-color: #ced4da;
  border-color: var(--button-border-color);
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  border-width: 2px;
  font-weight: 600;
  --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.button-control-container button:hover {
  background: linear-gradient(0.15turn, #f8f9fa, #e9ecef, #dee2e6);
  background: var(--button-hover);
}

.button-control-container span {
  font-size: calc(1px + 0.65rem);
  line-height: calc(0.5px + 0.75rem);
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  font-weight: 700;
}

.location-info-container {
  z-index: 3;
  top: 3.5rem;
  background-color: #dee2e6;
  background-color: var(--container-bg-color);
  opacity: 95%;
  height: max(15rem, 10vh);
  border-radius: 1rem 0rem 1rem 1rem;
  overflow: auto;
  position: relative;
  grid-column: span 2 / span 2;
  margin-left: 1rem;
  --tw-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.location-info-container h2 {
  color: #2e3151;
  color: var(--text-color);
  font-size: calc(4px + 0.95rem);
  line-height: calc(12px + 0.75rem);
  padding: 1rem;
  font-weight: 700;
}

.location-info-container ul {
  color: #2e3151;
  color: var(--text-color);
  font-size: calc(1px + 0.75rem);
  line-height: calc(8px + 0.75rem);
  padding: 0.75rem;
  font-weight: 600;
}

